@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&family=Roboto:ital,wght@0,300;0,400;0,500;0,900;1,700&display=swap');
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
.container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  min-height: 590px;
  width: 1000px;
  margin: 100px auto;
  padding: 50px 100px;
  background: center / cover no-repeat url('images/section-img.png');
}
.contantWrap {
  width: 50%;
}

.image {
  margin-top: 55px;
  display: block;
  height: 500px;
  border-radius: 3px;
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 1px rgb(0 0 0 / 14%),
    0 2px 1px rgb(0 0 0 / 20%);
}
.title {
  font-size: 50px;
  letter-spacing: 1.5px;
}
.subtitle {
  font-size: 30px;
  margin-top: 50px;
}
